.form {
    display: flex;
    flex-direction: column;
    color: #fff;
}

.formName,
.formNumber {
    display: flex;
    flex-direction: column;
    margin: 0.5rem auto;
    align-items: center;
    font-size: large;
    text-align: left;
    padding: 0.5rem 15rem 0.5rem 1rem;
    width: 45%;
    border-radius: 5px;
    border: none;
}

